<template>
    <el-form
        :rules="rules"
        class="login-container"
        label-position="left"
        label-width="0px"
        v-loading="loading"
    >
        <h3 class="login-title">系统登录</h3>
        <el-form-item prop="account">
            <el-input
                type="text"
                v-model="loginForm.username"
                placeholder="用户名"
                auto-complete="on"
                clearable
            ></el-input>
        </el-form-item>
        <el-form-item prop="checkPass">
            <el-input
                type="password"
                v-model="loginForm.password"
                placeholder="密码"
                auto-complete="on"
                clearable
            ></el-input>
        </el-form-item>
        <el-form-item style="width: 100%">
            <el-button type="primary" style="width: 100%" @click="submitLogin"
            >登录
            </el-button
            >
        </el-form-item>
    </el-form>
</template>

<script>
export default {
    name: "Login",
    data() {
        return {
            rules: {
                username: [
                    {required: true, message: "请输入用户名", trigger: "blur"},
                ],
                password: [{required: true, message: "请输入密码", trigger: "blur"}],
            },
            checked: true,
            loginForm: {
                username: "",
                password: "",
            },
            loading: false,
        };
    },
    methods: {
        submitLogin: function () {
            let _this = this
            this.loading = true
            _this.postRequest("oasys-back-end/login", {
                username: this.loginForm.username,
                password: this.loginForm.password,
            }).then(resp => {
                if (resp && resp.status == 200) {
                    this.loading = false
                    console.log(JSON.stringify(resp.data.object.username))
                    _this.$store.commit('initCurrentUser', resp.data.object)
                    window.localStorage.setItem("username", resp.data.object.username)
                    _this.$router.push("Home")
                } else {
                    _this.loading = false
                    //console.log(res && res.status)
                    console.log("笑死，根本进不去")
                }
            });
        },
    },
};
</script>

<style>
.login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 15px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
}

.login-title {
    margin: 15px auto 20px auto;
    text-align: center;
    color: #505458;
}

.login-remember {
    text-align: left;
    margin: 0px 0px 15px 0px;
}

.el-form-item__content {
    display: flex;
    align-items: center;
}
</style>



